*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

a {
  font-style: normal;
  text-decoration: none;
}

p {
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  margin: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

body {
  font-family: var(--main-font);
  letter-spacing: .04em;
  font-size: 16px;
  font-weight: 500;
}

.button {
  font: inherit;
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.container {
  max-width: 320px;
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

::-webkit-scrollbar {
  color: var(--brand-color);
  background-color: rgba(255, 165, 186, .5);
}

::-webkit-scrollbar-thumb {
  color: var(--accent-color);
  background-color: var(--accent-color);
  border-radius: 10px;
}

@keyframes changeBgColor {
  0% {
    background-color: rgba(198, 226, 151, .5);
  }

  50% {
    background-color: rgba(212, 20, 67, .5);
  }

  100% {
    background-color: #fbe9e9;
  }
}

@keyframes changeBgColorWhite {
  0% {
    background-color: #fff;
  }

  100% {
    background-color: #ffa5ba;
  }
}

:root {
  --main-font: "DM Sans", sans-serif;
  --secondary-font: "Titan One", cursive;
  --primary-text-color: #000;
  --secondary-text-color: #907e82;
  --third-text-color: #fff;
  --brand-color: #ffa5ba;
  --accent-color: #d41443;
  --header-background: #ffb8ca;
  --header-background-transparent: rgba(255, 184, 202, .7);
  --header-menu: rgba(225, 122, 146, .7);
  --header-anchor: #ecebeb;
  --accent-color-transparent: rgba(212, 20, 67, .7);
  --animation-function: cubic-bezier(.4, 0, .2, 1);
}

.header {
  z-index: 7;
  width: 100vw;
  background-color: var(--header-background-transparent);
  box-shadow: 0 8px 32px 0 var(--header-menu), inset 0 0 10px 5px var(--header-menu);
  -webkit-backdrop-filter: blur(6px);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border-bottom: 1px solid var(--header-menu);
  border-bottom-left-radius: 10%;
  border-bottom-right-radius: 10%;
  padding: 8px 20px;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .header {
    padding: 16px 32px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    padding: 19px 16px;
  }
}

.header__nav {
